










































































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			buildinf:wx.getStorageSync('buildinf'),
			vehiclelist:[],
			name:'',
			date:'',
			time:'',
			y:"",
			m:'',
			d:'',
			adress:{}
		}
	},
	onLoad(opt) {
		this.name=opt.name;
		console.log(wx.getStorageSync('buildinf'))
	},
	methods: {
		go_mine_adress(){
			wx.navigateTo({
				url: '../mine/adress'
			})
		},
		timeChange: function (e) {
			this.time=e.detail.value;
		},
		dateChange: function (e) {
			this.date=e.detail.value;
			const date=this.date.split('-');
			this.y=date[0];
			this.m=date[1];
			this.d=date[2];
		},
		req_unifiedOrder(){
			ut.request({
				data: {
					 "addressId": 10,
					  "makeTime": "2018.12.12",
					  "remark": "快点",
					  "serviceId": 2
				},
				url: "goods/order/unifiedOrder"
			}).then(data=>{
				ut.totast('下单成功')
			})
		},
		req_vehiclelist(){
			ut.request({
				data: {
					"addressId": 10,
						"makeTime": "2018.12.12",
						"remark": "快点",
						"serviceId": 2
				},
				url: "order/vehiclelist"
			}).then(data=>{
				this.vehiclelist=data;
			})
		}			
	}
}



